<template>
  <div>
    <div class="recommend2"  ref="wrappers">
      <slot></slot>
    </div>
  </div>

</template>

<script type="text/ecmascript-6">
  import BScroll from 'better-scroll'
  export default {
    mounted () {
      let this_=this;
      setTimeout(function () {
        this_.scroll = new BScroll(this_.$refs.wrappers,{mouseWheel: true, click: true, tap: true })
        console.log('this_.$refs.wrapperlistcity',this_.$refs.wrappers)
        this_.scroll.refresh()
      },1000);

      //
    },
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">

</style>



